import React, {component} from 'react';
import Header from './header';
import Naked from '.././images/naked.jpg'
import Team from './team';
import FAQ from './faq';
import Features from './features';
import Footer from './footer';
import Illus from '.././illustration/Photo Sharing-pana.svg';

function LandingPage(){
    return(
        <div> 
            <Header />

            <div className="about_img_widget">
                <div className="about_main">
                    <div className="about_text">
                            <h3> Header Text</h3>
                            <p>  Different problems faced in User connectivity in the use of the internet is Word
                                 abuse, harrasment, Scamming, Racial Abuse which often leads to Mental and emotional distress 
                                 to affected Users. In the society today, countless of Suicide cases have been linked to the use of Internet.

                            </p>

                            <a className="exp_btn" target="_blank" href="https://testphasenew.herokuapp.com/apidocs/?fbclid=IwAR09zUgERThg2213nGs0yGYbs0pzewL2XOx7H8lWcpET8hq-5mI7m39r_ho#/default/post_predict_image_file">
                                <button className="exp_btn">
                                        Test Algorithm 
                                    <i className="fi-rr-angle-right after_icon"></i>
                                </button>
                            </a>
                    </div>

                    <div class="about_illustration">
                        <img src={Illus} alt="Illustration" class="about_img" />
                    </div>
                </div>
            </div>

            <Features />

            <FAQ />

            <Team />

            <Footer />
        </div>
    );
}
export default LandingPage;