import React, { Component } from 'react';

class Footer extends Component{
    render(){
        return(
            <footer>
                    <div className="footer_left">
                        <h3>Navigation</h3>
                    </div>
                    <div className="footer_right">
                        <h3><i className="fi-rr-mobile before_icon"></i>Contact Us</h3>
                        <div className="footer_input_widget">
                            <input type="text" placeholder="Name" />
                            <input type="text" placeholder="Email Address" />
                        </div>

                        <textarea class="subject_input" type="text" placeholder="Subject" />
                        <button>Send Message <i className="fi-rr-paper-plane after_icon"></i></button>
                    </div>
            </footer>
        );
    }
}

export default Footer;